urface: #1f2937;\r\n  --surface-light: #374151;\r\n  --card: #283548;\r\n  \r\n  --text-primary: #f3f4f6;\r\n  --text-secondary: #d1d5db;\r\n  --text-tertiary: #9ca3af;\r\n  \r\n  --error: #ef4444;\r\n  --warning: #f59e0b;\r\n  --success: #22c55e;\r\n  \r\n  /* Spacing */\r\n  --spacing-xxs: 2px;\r\n  --spacing-xs: 4px;\r\n  --spacing-sm: 8px;\r\n  --spacing-md: 16px;\r\n  --spacing-lg: 24px;\r\n  --spacing-xl: 32px;\r\n  --spacing-xxl: 48px;\r\n  \r\n  /* Border radius */\r\n  --border-radius-sm: 6px;\r\n  --border-radius-md: 10px;\r\n  --border-radius-lg: 18px;\r\n  --border-radius-xl: 24px;\r\n  \r\n  /* Shadows */\r\n  --shadow-sm: 0 1px 3px rgba(0, 0, 0, 0.15), 0 1px 2px rgba(0, 0, 0, 0.2);\r\n  --shadow-md: 0 4px 6px rgba(0, 0, 0, 0.18), 0 2px 4px rgba(0, 0, 0, 0.22); \r\n  --shadow-lg: 0 10px 15px rgba(0, 0, 0, 0.2), 0 3px 6px rgba(0, 0, 0, 0.25);\r\n  --shadow-inner: inset 0 2px 4px rgba(0, 0, 0, 0.2);\r\n  --glow: 0 0 10px rgba(79, 70, 229, 0.6);\r\n  \r\n  /* Transitions */\r\n  --transition-fast: 150ms ease;\r\n  --transition-normal: 250ms ease;\r\n  --transition-slow: 400ms ease;\r\n}\r\n\r\n/* Global Styles */\r\nbody {\r\n  margin: 0;\r\n  padding: 0;\r\n  font-family: 'Segoe UI', -apple-system, BlinkMacSystemFont, Roboto, Oxygen,\r\n    Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;\r\n  background-color: var(--background);\r\n  color: var(--text-primary);\r\n  font-size: 16px;\r\n  line-height: 1.6;\r\n  overflow-x: hidden;\r\n}\r\n\r\n.rtl {\r\n  direction: rtl;\r\n  text-align: right;\r\n}\r\n\r\n/* Layout */\r\n.app-container {\r\n  display: flex;\r\n  flex-direction: column;\r\n  min-height: 100vh;\r\n  max-width: 1200px;\r\n  margin: 0 auto;\r\n  padding: 0 var(--spacing-md);\r\n}\r\n\r\n.content-wrapper {\r\n  padding: var(--spacing-md);\r\n  flex: 1;\r\n}\r\n\r\n/* Modern Header */\r\n.header {\r\n  display: flex;\r\n  align-items: center;\r\n  justify-content: space-between;\r\n  padding: var(--spacing-md) var(--spacing-lg);\r\n  background: linear-gradient(to right, var(--primary-dark), var(--primary));\r\n  border-radius: 0 0 var(--border-radius-lg) var(--border-radius-lg);\r\n  box-shadow: var(--shadow-md);\r\n  margin-bottom: var(--spacing-lg);\r\n  position: relative;\r\n  overflow: hidden;\r\n}\r\n\r\n.header::before {\r\n  content: '';\r\n  position: absolute;\r\n  top: 0;\r\n  left: 0;\r\n  right: 0;\r\n  bottom: 0;\r\n  background: radial-gradient(circle at 30% 107%, rgba(255, 255, 255, 0.05) 0%, rgba(255, 255, 255, 0) 80%);\r\n  pointer-events: none;\r\n}\r\n\r\n.header-title {\r\n  display: flex;\r\n  align-items: center;\r\n  gap: var(--spacing-sm);\r\n  font-weight: 600;\r\n  font-size: 1.8rem;\r\n  color: white;\r\n  text-shadow: 0 1px 2px rgba(0, 0, 0, 0.3);\r\n}\r\n\r\n.header-title svg {\r\n  font-size: 2rem;\r\n}\r\n\r\n.header-actions {\r\n  display: flex;\r\n  gap: var(--spacing-md);\r\n}\r\n\r\n/* Modern Cards */\r\n.card {\r\n  background: var(--card);\r\n  border-radius: var(--border-radius-md);\r\n  box-shadow: var(--shadow-md);\r\n  padding: var(--spacing-lg);\r\n  margin-bottom: var(--spacing-lg);\r\n  position: relative;\r\n  overflow: hidden;\r\n  border: 1px solid rgba(255, 255, 255, 0.05);\r\n  transition: transform var(--transition-normal), box-shadow var(--transition-normal);\r\n}\r\n\r\n.card:hover {\r\n  box-shadow: var(--shadow-lg);\r\n}\r\n\r\n.card h2, .card h3 {\r\n  margin-top: 0;\r\n  color: var(--text-primary);\r\n  font-weight: 600;\r\n  border-bottom: 1px solid rgba(255, 255, 255, 0.1);\r\n  padding-bottom: var(--spacing-sm);\r\n  margin-bottom: var(--spacing-md);\r\n}\r\n\r\n/* Form Elements */\r\n.form-group {\r\n  margin-bottom: var(--spacing-md);\r\n}\r\n\r\n.form-group label {\r\n  display: block;\r\n  margin-bottom: var(--spacing-xs);\r\n  color: var(--text-secondary);\r\n  font-weight: 500;\r\n  font-size: 0.95rem;\r\n}\r\n\r\n.input-group {\r\n  display: flex;\r\n  gap: var(--spacing-sm);\r\n}\r\n\r\ninput[type=\"text\"],\r\ninput[type=\"url\"],\r\ninput[type=\"number\"],\r\ntextarea {\r\n  background-color: var(--surface);\r\n  color: var(--text-primary);\r\n  border: 1px solid rgba(255, 255, 255, 0.1);\r\n  border-radius: var(--border-radius-sm);\r\n  padding: var(--spacing-sm) var(--spacing-md);\r\n  font-size: 1rem;\r\n  width: 100%;\r\n  transition: all var(--transition-fast);\r\n  box-shadow: var(--shadow-inner);\r\n}\r\n\r\ninput[type=\"text\"]:focus,\r\ninput[type=\"url\"]:focus,\r\ninput[type=\"number\"]:focus,\r\ntextarea:focus {\r\n  outline: none;\r\n  border-color: var(--primary-light);\r\n  box-shadow: 0 0 0 2px rgba(79, 70, 229, 0.25);\r\n}\r\n\r\ninput[type=\"text\"].error,\r\ninput[type=\"url\"].error {\r\n  border-color: var(--error);\r\n}\r\n\r\n/* Buttons */\r\n.btn {\r\n  display: inline-flex;\r\n  align-items: center;\r\n  justify-content: center;\r\n  gap: var(--spacing-xs);\r\n  padding: var(--spacing-sm) var(--spacing-md);\r\n  border-radius: var(--border-radius-sm);\r\n  font-weight: 500;\r\n  font-size: 0.9rem;\r\n  cursor: pointer;\r\n  transition: all var(--transition-fast);\r\n  border: none;\r\n  box-shadow: var(--shadow-sm);\r\n  white-space: nowrap;\r\n}\r\n\r\n.btn svg {\r\n  font-size: 1.2em;\r\n}\r\n\r\n.btn-primary {\r\n  background: linear-gradient(to bottom right, var(--primary-light), var(--primary));\r\n  color: white;\r\n}\r\n\r\n.btn-primary:hover, .btn-primary:focus {\r\n  background: linear-gradient(to bottom right, var(--primary), var(--primary-dark));\r\n  box-shadow: var(--shadow-md);\r\n  transform: translateY(-1px);\r\n}\r\n\r\n.btn-secondary {\r\n  background: var(--surface-light);\r\n  color: var(--text-primary);\r\n  border: 1px solid rgba(255, 255, 255, 0.1);\r\n}\r\n\r\n.btn-secondary:hover, .btn-secondary:focus {\r\n  background: var(--surface);\r\n  box-shadow: var(--shadow-md);\r\n  transform: translateY(-1px);\r\n}\r\n\r\n.btn-success {\r\n  background: linear-gradient(to bottom right, var(--accent-light), var(--accent));\r\n  color: white;\r\n}\r\n\r\n.btn-success:hover, .btn-success:focus {\r\n  background: linear-gradient(to bottom right, var(--accent), var(--accent-dark));\r\n  box-shadow: var(--shadow-md);\r\n  transform: translateY(-1px);\r\n}\r\n\r\n.btn-danger {\r\n  background: linear-gradient(to bottom right, #fb7185, #ef4444);\r\n  color: white;\r\n}\r\n\r\n.btn-danger:hover, .btn-danger:focus {\r\n  background: linear-gradient(to bottom right, #ef4444, #dc2626);\r\n  box-shadow: var(--shadow-md);\r\n  transform: translateY(-1px);\r\n}\r\n\r\n.btn-icon {\r\n  display: inline-flex;\r\n  align-items: center;\r\n  justify-content: center;\r\n  background: transparent;\r\n  border: none;\r\n  color: var(--text-tertiary);\r\n  cursor: pointer;\r\n  padding: var(--spacing-xs);\r\n  border-radius: 50%;\r\n  transition: all var(--transition-fast);\r\n}\r\n\r\n.btn-icon:hover, .btn-icon:focus {\r\n  color: var(--text-primary);\r\n  background: rgba(255, 255, 255, 0.1);\r\n  transform: scale(1.1);\r\n}\r\n\r\n.btn-icon svg {\r\n  font-size: 1.4rem;\r\n}\r\n\r\n.settings-button {\r\n  background: transparent;\r\n  border: none;\r\n  color: white;\r\n  opacity: 0.9;\r\n  cursor: pointer;\r\n  font-size: 1.5rem;\r\n  display: flex;\r\n  align-items: center;\r\n  justify-content: center;\r\n  border-radius: 50%;\r\n  width: 40px;\r\n  height: 40px;\r\n  transition: all var(--transition-fast);\r\n}\r\n\r\n.settings-button:hover {\r\n  opacity: 1;\r\n  background: rgba(255, 255, 255, 0.1);\r\n  transform: rotate(30deg);\r\n}\r\n\r\n/* Make language button match settings button */\r\n.language-button,\r\n.settings-button {\r\n  display: flex;\r\n  align-items: center;\r\n  justify-content: center;\r\n  background-color: transparent;\r\n  border: none;\r\n  border-radius: 50%;\r\n  color: var(--text-secondary);\r\n  width: 40px;\r\n  height: 40px;\r\n  margin-right: 8px;\r\n  cursor: pointer;\r\n  transition: all var(--transition-fast);\r\n  position: relative;\r\n  font-size: 1.2rem;\r\n}\r\n\r\n.language-button:hover,\r\n.settings-button:hover {\r\n  background-color: rgba(255, 255, 255, 0.05);\r\n  color: var(--text-primary);\r\n}\r\n\r\n.language-button.active {\r\n  color: var(--primary);\r\n}\r\n\r\n.language-button.active::after {\r\n  content: '';\r\n  position: absolute;\r\n  bottom: 2px;\r\n  left: 50%;\r\n  transform: translateX(-50%);\r\n  width: 6px;\r\n  height: 6px;\r\n  border-radius: 50%;\r\n  background-color: var(--primary);\r\n}\r\n\r\n/* Progress Bar */\r\n.progress-container {\r\n  background-color: var(--surface);\r\n  height: 8px;\r\n  border-radius: 4px;\r\n  overflow: hidden;\r\n  margin: var(--spacing-md) 0;\r\n  box-shadow: var(--shadow-inner);\r\n  position: relative;\r\n}\r\n\r\n.progress-bar {\r\n  height: 100%;\r\n  background: linear-gradient(to right, var(--primary-light), var(--primary));\r\n  transition: width 0.3s ease;\r\n  border-radius: 4px;\r\n}\r\n\r\n.progress-bar.completed {\r\n  background: linear-gradient(to right, var(--accent-light), var(--accent));\r\n}\r\n\r\n.progress-bar.error {\r\n  background: linear-gradient(to right, #fb7185, #ef4444);\r\n}\r\n\r\n.progress-shimmer {\r\n  position: absolute;\r\n  top: 0;\r\n  left: 0;\r\n  width: 100%;\r\n  height: 100%;\r\n  background: linear-gradient(\r\n    90deg,\r\n    transparent,\r\n    rgba(255, 255, 255, 0.2),\r\n    transparent\r\n  );\r\n  animation: shimmer 1.5s infinite;\r\n}\r\n\r\n@keyframes shimmer {\r\n  0% {\r\n    transform: translateX(-100%);\r\n  }\r\n  100% {\r\n    transform: translateX(100%);\r\n  }\r\n}\r\n\r\n/* Status badges */\r\n.badge {\r\n  display: inline-flex;\r\n  align-items: center;\r\n  gap: var(--spacing-xxs);\r\n  padding: var(--spacing-xs) var(--spacing-sm);\r\n  border-radius: var(--border-radius-sm);\r\n  font-size: 0.75rem;\r\n  font-weight: 600;\r\n  text-transform: uppercase;\r\n  letter-spacing: 0.5px;\r\n}\r\n\r\n.badge svg {\r\n  font-size: 1.1em;\r\n}\r\n\r\n.badge-queued {\r\n  background-color: var(--surface-light);\r\n  color: var(--text-secondary);\r\n}\r\n\r\n.badge-downloading {\r\n  background: linear-gradient(to right, var(--primary-light), var(--primary));\r\n  color: white;\r\n}\r\n\r\n.badge-paused {\r\n  background: linear-gradient(to right, var(--warning), #f97316);\r\n  color: #111;\r\n}\r\n\r\n.badge-completed {\r\n  background: linear-gradient(to right, var(--accent-light), var(--accent));\r\n  color: white;\r\n}\r\n\r\n.badge-error {\r\n  background: linear-gradient(to right, #fb7185, var(--error));\r\n  color: white;\r\n}\r\n\r\n/* Download Item */\r\n.download-item {\r\n  padding: var(--spacing-md);\r\n  border-radius: var(--border-radius-md);\r\n  background: var(--card);\r\n  margin-bottom: var(--spacing-md);\r\n  box-shadow: var(--shadow-md);\r\n  transition: transform 0.2s ease, box-shadow 0.2s ease;\r\n  border: 1px solid rgba(255, 255, 255, 0.05);\r\n}\r\n\r\n.download-item:hover {\r\n  transform: translateY(-2px);\r\n  box-shadow: var(--shadow-lg);\r\n}\r\n\r\n.download-header {\r\n  display: flex;\r\n  justify-content: space-between;\r\n  align-items: flex-start;\r\n  margin-bottom: var(--spacing-sm);\r\n}\r\n\r\n.download-filename {\r\n  font-weight: 600;\r\n  font-size: 1.1rem;\r\n  margin: 0;\r\n  word-break: break-word;\r\n  padding-right: var(--spacing-md);\r\n}\r\n\r\n.download-stats {\r\n  display: flex;\r\n  justify-content: space-between;\r\n  align-items: center;\r\n  font-size: 0.85rem;\r\n  color: var(--text-tertiary);\r\n  margin-bottom: var(--spacing-xs);\r\n  flex-wrap: wrap;\r\n  gap: var(--spacing-sm) var(--spacing-lg);\r\n}\r\n\r\n.download-stat {\r\n  display: flex;\r\n  align-items: center;\r\n  gap: var(--spacing-xs);\r\n}\r\n\r\n.download-stat svg {\r\n  font-size: 1.1em;\r\n  opacity: 0.7;\r\n}\r\n\r\n.download-actions {\r\n  display: flex;\r\n  flex-wrap: wrap;\r\n  gap: var(--spacing-sm);\r\n  margin-top: var(--spacing-md);\r\n}\r\n\r\n.file-operations {\r\n  display: flex;\r\n  margin-left: auto;\r\n  gap: var(--spacing-sm);\r\n}\r\n\r\n/* Tooltips */\r\n.tooltip-container {\r\n  position: relative;\r\n  display: inline-block;\r\n}\r\n\r\n.help-icon {\r\n  display: inline-flex;\r\n  align-items: center;\r\n  justify-content: center;\r\n  width: 18px;\r\n  height: 18px;\r\n  border-radius: 50%;\r\n  background-color: var(--surface-light);\r\n  color: var(--text-secondary);\r\n  font-size: 12px;\r\n  margin-left: var(--spacing-xs);\r\n  cursor: help;\r\n}\r\n\r\n.tooltip-text {\r\n  visibility: hidden;\r\n  position: absolute;\r\n  z-index: 1;\r\n  bottom: 125%;\r\n  left: 50%;\r\n  transform: translateX(-50%);\r\n  background-color: var(--surface);\r\n  color: var(--text-secondary);\r\n  text-align: center;\r\n  border-radius: var(--border-radius-sm);\r\n  padding: var(--spacing-sm) var(--spacing-md);\r\n  width: 280px;\r\n  box-shadow: var(--shadow-lg);\r\n  opacity: 0;\r\n  transition: opacity var(--transition-normal);\r\n  font-size: 0.85rem;\r\n  font-weight: normal;\r\n  line-height: 1.4;\r\n  border: 1px solid rgba(255, 255, 255, 0.1);\r\n}\r\n\r\n.tooltip-text::after {\r\n  content: \"\";\r\n  position: absolute;\r\n  top: 100%;\r\n  left: 50%;\r\n  margin-left: -5px;\r\n  border-width: 5px;\r\n  border-style: solid;\r\n  border-color: var(--surface) transparent transparent transparent;\r\n}\r\n\r\n.tooltip-container:hover .tooltip-text {\r\n  visibility: visible;\r\n  opacity: 1;\r\n}\r\n\r\n/* Dialog */\r\n.dialog-overlay {\r\n  position: fixed;\r\n  top: 0;\r\n  left: 0;\r\n  right: 0;\r\n  bottom: 0;\r\n  background-color: rgba(0, 0, 0, 0.7);\r\n  display: flex;\r\n  align-items: center;\r\n  justify-content: center;\r\n  z-index: 1000;\r\n  backdrop-filter: blur(4px);\r\n}\r\n\r\n.dialog {\r\n  background: var(--surface);\r\n  border-radius: var(--border-radius-lg);\r\n  padding: var(--spacing-lg);\r\n  width: 90%;\r\n  max-width: 550px;\r\n  max-height: 90vh;\r\n  overflow-y: auto;\r\n  box-shadow: var(--shadow-lg);\r\n  position: relative;\r\n  animation: dialogEnter 0.3s ease;\r\n}\r\n\r\n@keyframes dialogEnter {\r\n  from {\r\n    opacity: 0;\r\n    transform: scale(0.95);\r\n  }\r\n  to {\r\n    opacity: 1;\r\n    transform: scale(1);\r\n  }\r\n}\r\n\r\n.dialog h2 {\r\n  margin-top: 0;\r\n  color: var(--text-primary);\r\n  border-bottom: 1px solid rgba(255, 255, 255, 0.1);\r\n  padding-bottom: var(--spacing-sm);\r\n  margin-bottom: var(--spacing-lg);\r\n}\r\n\r\n.dialog-buttons {\r\n  display: flex;\r\n  justify-content: flex-end;\r\n  gap: var(--spacing-md);\r\n  margin-top: var(--spacing-lg);\r\n}\r\n\r\n/* Message Bars */\r\n.message-bar {\r\n  padding: var(--spacing-md);\r\n  border-radius: var(--border-radius-sm);\r\n  margin-bottom: var(--spacing-md);\r\n  display: flex;\r\n  align-items: center;\r\n  gap: var(--spacing-sm);\r\n}\r\n\r\n.message-bar.info {\r\n  background: rgba(59, 130, 246, 0.1);\r\n  border-left: 4px solid #3b82f6;\r\n  color: #93c5fd;\r\n}\r\n\r\n.message-bar.warning {\r\n  background: rgba(245, 158, 11, 0.1);\r\n  border-left: 4px solid #f59e0b;\r\n  color: #fcd34d;\r\n}\r\n\r\n.message-bar.error {\r\n  background: rgba(239, 68, 68, 0.1);\r\n  border-left: 4px solid #ef4444;\r\n  color: #fca5a5;\r\n}\r\n\r\n.message-bar.success {\r\n  background: rgba(16, 185, 129, 0.1);\r\n  border-left: 4px solid #10b981;\r\n  color: #6ee7b7;\r\n}\r\n\r\n/* Empty State */\r\n.empty-state {\r\n  display: flex;\r\n  flex-direction: column;\r\n  align-items: center;\r\n  justify-content: center;\r\n  text-align: center;\r\n  padding: var(--spacing-xxl) var(--spacing-md);\r\n  color: var(--text-tertiary);\r\n}\r\n\r\n.empty-state svg {\r\n  font-size: 4rem;\r\n  margin-bottom: var(--spacing-md);\r\n  opacity: 0.6;\r\n}\r\n\r\n.empty-state h3 {\r\n  margin: var(--spacing-sm) 0;\r\n  color: var(--text-secondary);\r\n}\r\n\r\n.empty-state p {\r\n  max-width: 400px;\r\n  margin: 0 auto;\r\n}\r\n\r\n/* Animation */\r\n@keyframes pulse {\r\n  0% {\r\n    opacity: 1;\r\n  }\r\n  50% {\r\n    opacity: 0.7;\r\n  }\r\n  100% {\r\n    opacity: 1;\r\n  }\r\n}\r\n\r\n.pulse {\r\n  animation: pulse 1.5s infinite ease-in-out;\r\n}\r\n\r\n@keyframes highlightAnimation {\r\n  0% {\r\n    background-color: rgba(79, 70, 229, 0.1);\r\n  }\r\n  100% {\r\n    background-color: transparent;\r\n  }\r\n}\r\n\r\n.highlight-animation {\r\n  animation: highlightAnimation 2s ease;\r\n}\r\n\r\n/* Language-specific */\r\nhtml[dir=\"rtl\"] .header {\r\n  background: linear-gradient(to left, var(--primary-dark), var(--primary));\r\n}\r\n\r\nhtml[dir=\"rtl\"] .progress-bar {\r\n  background: linear-gradient(to left, var(--primary-light), var(--primary));\r\n}\r\n\r\nhtml[dir=\"rtl\"] .badge-downloading {\r\n  background: linear-gradient(to left, var(--primary-light), var(--primary));\r\n}\r\n\r\nhtml[dir=\"rtl\"] .badge-paused {\r\n  background: linear-gradient(to left, var(--warning), #f97316);\r\n}\r\n\r\nhtml[dir=\"rtl\"] .badge-completed {\r\n  background: linear-gradient(to left, var(--accent-light), var(--accent));\r\n}\r\n\r\nhtml[dir=\"rtl\"] .badge-error {\r\n  background: linear-gradient(to left, #fb7185, var(--error));\r\n}\r\n\r\n/* Media Queries */\r\n@media (max-width: 768px) {\r\n  .header {\r\n    padding: var(--spacing-sm) var(--spacing-md);\r\n  }\r\n  \r\n  .header-title {\r\n    font-size: 1.4rem;\r\n  }\r\n\r\n  .card {\r\n    padding: var(--spacing-md);\r\n  }\r\n  \r\n  .download-actions {\r\n    flex-direction: column;\r\n    align-items: stretch;\r\n  }\r\n  \r\n  .file-operations {\r\n    margin-left: 0;\r\n    margin-top: var(--spacing-sm);\r\n  }\r\n  \r\n  .dialog {\r\n    width: 95%;\r\n    padding: var(--spacing-md);\r\n  }\r\n}\r\n\r\n/* Language menu */\r\n.language-menu-backdrop {\r\n  position: fixed;\r\n  top: 0;\r\n  left: 0;\r\n  right: 0;\r\n  bottom: 0;\r\n  z-index: 1500;\r\n  display: flex;\r\n  justify-content: center;\r\n  align-items: flex-start;\r\n  padding-top: 80px;\r\n}\r\n\r\n.language-menu-floating {\r\n  background-color: var(--surface);\r\n  border-radius: var(--border-radius-md);