___CSS_LOADER_EXPORT___.push([module.id, `/* The dialog selectors are now handled in modern.css */

/* Category container */
.category-container {
  display: flex;
  align-items: center;
  gap: var(--spacing-sm);
}

.category-container select {
  flex: 1;
  background-color: var(--surface);
  color: var(--text-primary);
  border: 1px solid rgba(255, 255, 255, 0.1);
  border-radius: var(--border-radius-sm);
  padding: var(--spacing-sm) var(--spacing-md);
  font-size: 1rem;
  box-shadow: var(--shadow-inner);
}

.add-category-btn {
  display: flex;
  align-items: center;
  justify-content: center;
  background: transparent;
  border: none;
  color: var(--text-tertiary);
  cursor: pointer;
  width: 36px;
  height: 36px;
  border-radius: 50%;
  transition: all var(--transition-fast);
}

.add-category-btn:hover {
  color: var(--primary);
  background: rgba(255, 255, 255, 0.05);
}

.add-category-btn svg {
  font-size: 1.2rem;
}

/* Save path container */
.save-path-container {
  display: flex;
  gap: var(--spacing-sm);
}

.save-path-container input {
  flex: 1;
}

/* Remember path checkbox styling */
.remember-path {
  display: flex;
  align-items: center;
  margin-top: var(--spacing-xs);
  color: var(--text-tertiary);
  font-size: 0.9rem;
}

.remember-path input[type="checkbox"] {
  margin-right: var(--spacing-sm);
}

/* Readonly inputs */
.readonly-input {
  background-color: var(--surface-light) !important;
  opacity: 0.8;
  cursor: not-allowed;
}

/* Existing download info styling */
.existing-download-info {
  background: var(--surface);
  border-radius: var(--border-radius-sm);
  padding: var(--spacing-md);
  margin: var(--spacing-md) 0;
  display: flex;
  flex-direction: column;
  gap: var(--spacing-sm);
  border-left: 4px solid var(--primary);
}

.existing-download-info strong {
  color: var(--text-secondary);
  margin-right: var(--spacing-sm);
}

/* Tertiary button (different from primary/secondary in modern.css) */
.btn-tertiary {
  background: transparent;
  color: var(--text-secondary);
  border: 1px solid rgba(255, 255, 255, 0.1);
}

.btn-tertiary:hover, .btn-tertiary:focus {
  background: rgba(255, 255, 255, 0.05);
  color: var(--text-primary);
}

/* Animation for the dialog */
.download-confirm-dialog {
  animation: slideDown 0.3s ease;
}

@keyframes slideDown {
  from {
    transform: translateY(-20px);
    opacity: 0;
  }
  to {
    transform: translateY(0);
    opacity: 1;
  }
}

/* RTL Specific Styles */
html[dir="rtl"] .existing-download-info {
  border-left: none;
  border-right: 4px solid var(--primary);
}

/* Mobile Styles */
@media (max-width: 500px) {
  .dialog-buttons {
    flex-direction: column;
    align-items: stretch;
  }
  
  .dialog-buttons button {
    margin-bottom: var(--spacing-sm);
  }
} `, "",{"version":3,"sources":["webpack://./src/renderer/components/DownloadConfirmDialog.css"],"names":[],"mappings":"AAAA,uDAAuD;;AAEvD,uBAAuB;AACvB;EACE,aAAa;EACb,mBAAmB;EACnB,sBAAsB;AACxB;;AAEA;EACE,OAAO;EACP,gCAAgC;EAChC,0BAA0B;EAC1B,0CAA0C;EAC1C,sCAAsC;EACtC,4CAA4C;EAC5C,eAAe;EACf,+BAA+B;AACjC;;AAEA;EACE,aAAa;EACb,mBAAmB;EACnB,uBAAuB;EACvB,uBAAuB;EACvB,YAAY;EACZ,2BAA2B;EAC3B,eAAe;EACf,WAAW;EACX,YAAY;EACZ,kBAAkB;EAClB,sCAAsC;AACxC;;AAEA;EACE,qBAAqB;EACrB,qCAAqC;AACvC;;AAEA;EACE,iBAAiB;AACnB;;AAEA,wBAAwB;AACxB;EACE,aAAa;EACb,sBAAsB;AACxB;;AAEA;EACE,OAAO;AACT;;AAEA,mCAAmC;AACnC;EACE,aAAa;EACb,mBAAmB;EACnB,6BAA6B;EAC7B,2BAA2B;EAC3B,iBAAiB;AACnB;;AAEA;EACE,+BAA+B;AACjC;;AAEA,oBAAoB;AACpB;EACE,iDAAiD;EACjD,YAAY;EACZ,mBAAmB;AACrB;;AAEA,mCAAmC;AACnC;EACE,0BAA0B;EAC1B,sCAAsC;EACtC,0BAA0B;EAC1B,2BAA2B;EAC3B,aAAa;EACb,sBAAsB;EACtB,sBAAsB;EACtB,qCAAqC;AACvC;;AAEA;EACE,4BAA4B;EAC5B,+BAA+B;AACjC;;AAEA,qEAAqE;AACrE;EACE,uBAAuB;EACvB,4BAA4B;EAC5B,0CAA0C;AAC5C;;AAEA;EACE,qCAAqC;EACrC,0BAA0B;AAC5B;;AAEA,6BAA6B;AAC7B;EACE,8BAA8B;AAChC;;AAEA;EACE;IACE,4BAA4B;IAC5B,UAAU;EACZ;EACA;IACE,wBAAwB;IACxB,UAAU;EACZ;AACF;;AAEA,wBAAwB;AACxB;EACE,iBAAiB;EACjB,sCAAsC;AACxC;;AAEA,kBAAkB;AAClB;EACE;IACE,sBAAsB;IACtB,oBAAoB;EACtB;;EAEA;IACE,gCAAgC;EAClC;AACF","sourcesContent":["/* The dialog selectors are now handled in modern.css */\r\n\r\n/* Category container */\r\n.category-container {\r\n  display: flex;\r\n  align-items: center;\r\n  gap: var(--spacing-sm);\r\n}\r\n\r\n.category-container select {\r\n  flex: 1;\r\n  background-color: var(--surface);\r\n  color: var(--text-primary);\r\n  border: 1px solid rgba(255, 255, 255, 0.1);\r\n  border-radius: var(--border-radius-sm);\r\n  padding: var(--spacing-sm) var(--spacing-md);\r\n  font-size: 1rem;\r\n  box-shadow: var(--shadow-inner);\r\n}\r\n\r\n.add-category-btn {\r\n  display: flex;\r\n  align-items: center;\r\n  justify-content: center;\r\n  background: transparent;\r\n  border: none;\r\n  color: var(--text-tertiary);\r\n  cursor: point